.gallery {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-evenly;
	margin-left: auto;
	margin-right: auto;
	padding: 40px;
 }
 
 .card-link {
	text-decoration: none;
	color: inherit;
 }
 
 .photo-card {
	background-color: #fff;
	border-radius: 10px;
	box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
	transition: transform 250ms linear;
	margin-bottom: 20px;
	cursor: pointer;
 }
 
 .photo-card:hover {
	transform: scale(1.05);
 }
 
 .thumb {
	width: 340px;
	height: 220px;
 
	object-fit: cover;
 }
 
 .photo {
	display: block;
	width: 100%;
	height: 100%;
	border-top-left-radius: 10px;
	border-top-right-radius: 10px;
 }
 
 .info {
	display: flex;
	justify-content: space-between;
	padding: 15px;
 }
 
 .info-item {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 0;
 }